::-webkit-scrollbar-button {
  background-image: url("");
  background-repeat: no-repeat;
  width: 7px;
  height: 0px;
}

::-webkit-scrollbar-track {
  background-color: #fff;
}

::-webkit-scrollbar-thumb {
  background-color: #132F57;
  box-shadow: 0px 1px 1px #fff inset;
  border-radius: 100px;
}

::-webkit-resizer {
  background-image: url("");
  background-repeat: no-repeat;
  width: 7px;
  height: 0px;
}

::-webkit-scrollbar {
  width: 7px;
  height: 7px;
}

.header {
  font-size: 14px;
}
@media screen and (max-width: 1200px) {
  .header {
    font-family: "Bebas Neue", sans-serif;
    font-size: 30px;
  }
}
.header.light {
  color: #fff;
}
.header.light a {
  color: #fff;
}
.header.dark {
  color: #132F57;
}
@media screen and (max-width: 1200px) {
  .header.dark {
    color: #fff;
  }
}
.header.dark path {
  stroke: #132F57;
}
.header.dark a {
  color: #132F57;
}
@media screen and (max-width: 1200px) {
  .header.dark a {
    color: #fff;
  }
}
.header.dark .active svg path {
  stroke: #fff;
}
.header.atop {
  min-height: 130px;
  margin-bottom: -180px;
  position: relative;
  z-index: 200;
}
@media screen and (max-width: 1200px) {
  .header.atop {
    position: absolute;
    width: 100%;
  }
}
.header__nav {
  display: flex;
  align-items: center;
  padding-top: 72px;
  padding-bottom: 50px;
}
@media screen and (max-width: 1200px) {
  .header__nav {
    padding-top: 50px;
  }
}
.header__nav-box {
  display: flex;
  padding-left: 0;
}
.header__nav-box-logo img {
  display: none;
}
@media screen and (max-width: 1200px) {
  .header__nav-box-logo img {
    display: block;
    position: absolute;
    top: 60px;
    height: 80px;
  }
}
@media screen and (max-width: 1200px) {
  .header__nav-box-company {
    display: none;
  }
}
.header__nav-box-contacts {
  display: none;
}
@media screen and (max-width: 1200px) {
  .header__nav-box-contacts {
    display: block;
    font-family: "Montserrat", sans-serif;
    position: absolute;
    bottom: 20px;
    margin: 0;
    padding: 0;
  }
  .header__nav-box-contacts li {
    font-size: 13px;
    padding: 0;
  }
  .header__nav-box-contacts li a {
    border-bottom: 2px dotted #13b7aa;
  }
}
@media screen and (max-width: 1200px) {
  .header__nav-box {
    position: fixed;
    z-index: 100;
    width: 100%;
    height: 100%;
    background-color: #132F57;
    left: 0;
    top: -100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    transition: 1s;
    opacity: 0;
    padding: 20px;
  }
  .header__nav-box li {
    padding: 5px;
  }
}
.header__nav li {
  display: inline-block;
  padding-right: 45px;
}
@media screen and (max-width: 1200px) {
  .header__nav li {
    padding-right: 0px;
  }
}
.header__nav a {
  text-decoration: none;
}
.header__nav a:hover {
  text-decoration: underline;
}
.header__nav-logo img {
  height: 55px;
  transition: 0.5s ease-out;
}
@media screen and (max-width: 1200px) {
  .header__nav-logo img {
    height: 90px;
  }
}
.header__nav-logo img:hover {
  transform: scale(1.05);
}
.header .nav__ico {
  display: none;
}
.header .nav__ico.dark path {
  stroke: #132F57;
}
.header .nav__ico.dark.active path {
  stroke: #fff !important;
}
@media screen and (max-width: 1200px) {
  .header .nav__ico {
    cursor: pointer;
    display: flex;
    z-index: 1000;
    position: fixed;
    right: 0;
    top: 15px;
  }
}
.header .active ~ ul.header__nav-box {
  top: 0;
  opacity: 1;
}
.header svg {
  transition: transform 500ms cubic-bezier(0.4, 0, 0.2, 1);
}
.header .active svg {
  transform: rotate(90deg);
}
.header path {
  transition: transform 500ms cubic-bezier(0.4, 0, 0.2, 1), stroke-dasharray 500ms cubic-bezier(0.4, 0, 0.2, 1), stroke-dashoffset 500ms cubic-bezier(0.4, 0, 0.2, 1);
  stroke: #fff;
}
.header path:nth-child(1) {
  transform-origin: 36% 40%;
}
.header path:nth-child(2) {
  stroke-dasharray: 29 299;
}
.header path:nth-child(3) {
  transform-origin: 35% 63%;
}
.header path:nth-child(4) {
  stroke-dasharray: 29 299;
}
.header path:nth-child(5) {
  transform-origin: 61% 52%;
}
.header path:nth-child(6) {
  transform-origin: 62% 52%;
}
.header .active path:nth-child(1) {
  transform: translateX(9px) translateY(1px) rotate(45deg);
}
.header .active path:nth-child(2) {
  stroke-dasharray: 225 299;
  stroke-dashoffset: -72px;
}
.header .active path:nth-child(3) {
  transform: translateX(9px) translateY(1px) rotate(-45deg);
}
.header .active path:nth-child(4) {
  stroke-dasharray: 225 299;
  stroke-dashoffset: -72px;
}
.header .active path:nth-child(5) {
  transform: translateX(9px) translateY(1px) rotate(-45deg);
}
.header .active path:nth-child(6) {
  transform: translateX(9px) translateY(1px) rotate(45deg);
}

.footer {
  margin-top: 110px;
  padding-top: 50px;
  color: #fff;
  position: relative;
  overflow: hidden;
}
.footer .container {
  position: relative;
  z-index: 10;
}
.footer.sea {
  background: linear-gradient(121.07deg, #13b7aa -2.76%, #132F57 87.26%);
}
.footer.sea::after {
  top: 0;
  left: 0;
  content: "";
  display: block;
  position: absolute;
  width: 400px;
  height: 100%;
  background-position: 869px 65%;
  background-image: url("/img/bg-sea.png");
  background-image: url("/img/bg-sea.webp");
  z-index: 1;
}
.footer.sky {
  background: linear-gradient(121.07deg, #3C9CD7 -2.76%, #132F57 87.26%);
}
.footer.sky::after {
  top: 0;
  left: 0;
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-position: 100% 0%;
  background-image: url("/img/bg-sky.png");
  background-image: url("/img/bg-sky.webp");
  background-repeat: no-repeat;
  background-size: 800px;
  z-index: 1;
}
.footer__logo {
  max-width: 90px;
  width: 100%;
}
.footer ul {
  padding: 0;
  padding-bottom: 30px;
  padding-right: 20px;
}
.footer li {
  display: block;
}
.footer a {
  color: #fff;
}

.page-top {
  overflow: hidden;
  margin-bottom: -60px;
  position: relative;
  border-radius: 0 0 60px 60px;
  z-index: 100;
}
.page-top h1,
.page-top h2 {
  color: #fff;
}
@media screen and (max-width: 1200px) {
  .page-top h1,
.page-top h2 {
    font-size: 30px;
  }
}
.page-top__box {
  overflow: hidden;
  max-height: 785px;
  height: 100vh;
  position: relative;
  color: #fff;
  display: flex;
  overflow: hidden;
}
@media screen and (max-width: 1200px) {
  .page-top__box {
    height: 450px;
  }
}
.page-top__box-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
@media screen and (max-width: 1200px) {
  .page-top__box-content {
    padding-top: 80px;
  }
}
.page-top__box-bg {
  position: absolute;
  width: 80%;
  height: 100%;
  left: 10%;
  top: 140px;
  border-radius: 30px;
  background-position: center;
  background-size: cover;
  transition: 0.5s ease-out;
}
@media screen and (max-width: 1200px) {
  .page-top__box-bg {
    width: 100%;
    border-radius: 0;
    top: 0;
    left: 0;
  }
}
@media screen and (max-width: 1200px) {
  .page-top__box.left {
    position: relative;
    z-index: 100;
  }
}
@media screen and (max-width: 1200px) {
  .page-top__box.right {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    opacity: 0.3;
    left: 0;
  }
}
.page-top__box.sea {
  background: rgba(19, 183, 170, 0.4);
}
.page-top__box.sky {
  background: rgba(60, 156, 215, 0.4);
}
.page-top.sea {
  justify-content: flex-end;
  background: linear-gradient(121.07deg, #13b7aa -2.76%, #132F57 87.26%);
}
.page-top.sea::before {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-position: center;
  background-image: url("/img/bg-sea.png");
  background-image: url("/img/bg-sea.webp");
  transition: all 0.5s ease-out 0s;
}
@media screen and (max-width: 1200px) {
  .page-top.sea::before {
    display: none;
  }
}
.page-top.sea .page-main__box-content {
  padding-right: 30px;
}
.page-top.sky {
  background: linear-gradient(121.07deg, #3C9CD7 -2.76%, #132F57 87.26%);
}
.page-top.sky::before {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-position: top right;
  background-repeat: no-repeat;
  background-image: url("/img/bg-sky.png");
  background-image: url("/img/bg-sky.webp");
  background-size: contain;
  transition: all 0.5s ease-out 0s;
}
@media screen and (max-width: 1200px) {
  .page-top.sky::before {
    display: none;
  }
}
.page-top.sky .page-main__box-content {
  padding-left: 80px;
}

.quote {
  margin: 0;
}
.quote__box {
  padding: 100px;
  margin: 110px 0;
  border-radius: 30px;
  position: relative;
  font-size: 26px;
}
.quote__box p {
  position: relative;
  z-index: 10;
}
@media screen and (max-width: 1200px) {
  .quote__box {
    font-size: 16px;
    padding: 100px 25px;
  }
  .quote__box::before {
    left: 20px !important;
  }
}
.quote__box::before {
  content: "";
  font-size: 200px;
  font-family: "Bebas Neue", sans-serif;
  position: absolute;
  top: -17px;
  left: 45px;
}
.quote__box small {
  font-size: 16px;
}
.quote.sea {
  background-color: #E4FAF9;
  color: #fff;
}
.quote.sea .quote__box {
  background: linear-gradient(121.07deg, #13b7aa -2.76%, #132F57 87.26%);
}
.quote.sea .quote__box::after {
  top: 0;
  left: 0;
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-position: 49% 62%;
  background-image: url("/img/bg-sea.png");
  background-image: url("/img/bg-sea.webp");
}
.quote.sky {
  background-color: rgba(60, 156, 215, 0.4);
  color: #fff;
}
.quote.sky .quote__box {
  background: linear-gradient(121.07deg, #3C9CD7 -2.76%, #132F57 87.26%);
}
.quote.sky .quote__box::after {
  top: 0;
  left: 0;
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-position: 100% 62%;
  background-image: url("/img/bg-sky.png");
  background-image: url("/img/bg-sky.webp");
  background-size: 800px;
  background-repeat: no-repeat;
}

.dealer {
  padding-bottom: 110px;
}
.dealer h2 {
  padding: 0;
  margin: 0;
}
.dealer__form {
  padding-top: 10px;
}
.dealer__form input {
  width: 100%;
  margin-bottom: 10px;
}

.partners {
  padding: 110px 0;
}
.partners__logo {
  display: flex;
  justify-content: center;
  text-align: center;
}
.partners__logo img {
  padding: 20px 50px;
}
@media screen and (max-width: 1000px) {
  .partners__logo img {
    width: 70%;
    padding: 0;
    padding-top: 30px;
  }
}

.principles {
  padding: 100px 0;
}
.principles.base {
  background-color: #F9F9F9;
}
.principles.sky {
  background-color: #E4F2FA;
}
.principles.sea {
  background-color: #E4FAF9;
}
.principles h2 {
  margin-top: 0;
}
.principles__box {
  display: grid;
  margin: 0px;
  padding: 0px;
  grid-template-columns: 1fr 1fr 1fr;
  grid-auto-rows: 125px;
  grid-gap: 29px;
  list-style-type: none;
  counter-reset: num;
}
@media screen and (max-width: 1200px) {
  .principles__box {
    grid-template-columns: 1fr 1fr;
  }
}
@media screen and (max-width: 900px) {
  .principles__box {
    grid-template-columns: 1fr;
    grid-auto-rows: minmax(100px, auto);
  }
}
.principles__box-item {
  color: #fff;
  border-radius: 15px;
  padding: 10px 50px;
  display: flex;
  align-items: center;
  font-weight: 700;
  position: relative;
}
.principles__box-item::before {
  content: counter(num);
  counter-increment: num;
  position: absolute;
  display: inline-flex;
  width: 47px;
  height: 47px;
  background: #fff;
  color: #132F57;
  align-items: center;
  justify-content: center;
  font-size: 40px;
  border-radius: 30px;
  font-weight: 700;
  top: -21px;
  left: 5px;
  font-family: "Bebas Neue", sans-serif;
}
.principles__box-item.sky {
  background: #3C9CD7;
}
.principles__box-item.sea {
  background: #13b7aa;
}

