.content-product {
  padding: 100px 0;
}
.content-product .grid {
  display: grid;
  grid-template-areas: "A B B" "A C C";
  grid-template-columns: 400px auto auto;
  grid-gap: 20px;
}
@media screen and (max-width: 1000px) {
  .content-product .grid {
    grid-template-areas: "B B B" "A A A" "C C C";
    grid-template-columns: auto;
  }
}
.content-product .grid-box__title-img {
  grid-area: A;
  overflow: hidden;
  border-radius: 30px;
  box-shadow: 5px 4px 10px rgba(51, 51, 51, 0.05);
  text-align: center;
}
.content-product .grid-box__title-img img {
  height: 100%;
}
.content-product .grid-box__text {
  grid-area: B;
  padding-left: 10px;
}
.content-product .grid-box__img {
  grid-area: C;
  display: grid;
  column-gap: 20px;
  grid-template-columns: 1fr 1fr;
}
.content-product .grid-box__img-bg {
  background: linear-gradient(180deg, #EAEAEA 0%, #CBCBCB 100%);
  border-radius: 30px;
  overflow: hidden;
  text-align: center;
}
.content-product .grid-box__img-bg img {
  height: 100%;
}

