@media screen and (max-width: 1200px) {
  .product-info__box-text {
    padding: 25px;
  }
}
.product-info__box-text.sea {
  background-color: #E4FAF9;
}
.product-info__box-text.sky {
  background-color: #E4F2FA;
}
.product-info__box-text.base {
  background-color: #F9F9F9;
}
.product-info__box-img {
  border-radius: 30px;
  background-size: contain;
  background-position: center bottom;
  min-height: 450px;
  background-repeat: no-repeat;
  background-color: #fff;
}

.goods {
  overflow: hidden;
}
.goods h2 {
  position: relative;
}
.goods h2::before {
  content: "";
  position: absolute;
  width: 50px;
  height: 50px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url(/img/svg/toch.svg);
  right: 0;
}
@media screen and (max-width: 1000px) {
  .goods h2::before {
    display: none;
  }
}
.goods__box {
  width: 100%;
  right: 0;
  height: 650px;
  display: flex;
  flex-wrap: nowrap;
  overflow: hidden;
  position: relative;
}
@media screen and (max-width: 1000px) {
  .goods__box {
    height: auto;
    overflow: auto;
    flex-direction: column;
  }
}
.goods__box-item {
  background: linear-gradient(180deg, #FFFFFF -39.36%, #E5E5E5 178.87%);
  width: 500px;
  height: 620px;
  border-radius: 15px;
  flex: 0 0 auto;
  padding: 30px;
  padding-top: 20px;
  padding-bottom: 5px;
  margin-right: 50px;
  display: flex;
  flex-direction: column;
  color: #132F57;
  line-height: 30px;
}
@media screen and (max-width: 1000px) {
  .goods__box-item {
    width: calc(100vw - 20px);
    margin: auto;
    margin: 0px 10px;
    max-width: 500px;
    height: 720px;
    padding: 0px 20px;
    padding-top: 20px;
  }
}
.goods__box-item.empty {
  width: calc(50vw - 660px);
  height: 620px;
  background: transparent;
  margin-right: 0px;
}
@media screen and (max-width: 1000px) {
  .goods__box-item.empty {
    display: none;
  }
}
.goods__box-item h3 {
  font-size: 22px;
}
@media screen and (max-width: 1000px) {
  .goods__box-item h3 {
    font-size: 18px;
  }
}
.goods__box-item-bg {
  width: 100%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

@media screen and (max-width: 1000px) {
  .owl-carousel .owl-stage {
    display: grid;
    transform: none !important;
    width: 100% !important;
    justify-items: center;
  }

  .goods__box-item {
    margin-top: 20px;
  }
}